.card-container {
    display: flex;
    flex-direction: row;
    overflow-x: auto;
    white-space: nowrap;
    padding: 1rem; /* Optional: Add some padding for better appearance */
  }
  
  .card-item {
    flex: 0 0 auto; /* Ensure the cards don't shrink or grow */
    margin-right: 16px; /* Add some spacing between the cards */
  }